import React, {Component} from 'react';
import {Prism as SyntaxHighlighter} from "react-syntax-highlighter";
import {coy} from "react-syntax-highlighter/dist/cjs/styles/prism";
import Markdown from "react-markdown";

class Mark extends Component {
    state={
        markdowntext:[
            {
                path:"/about_me",
                text:"# about me"
            },
            {
                path:"/array_method",
                text:"# javascript base"
            },
            {
                path:"/Scope",
                text:"# \n" +
                    "varletconst\n" +
                    "* {}varletconst\n" +
                    "* varletconst\n" +
                    "* \n" +
                    "```javascript\n" +
                    "//\n" +
                    "let funArr = [];\n" +
                    "for(var i = 0;i<5;i++){\n" +
                    "    funArr.push(function(){\n" +
                    "        console.log(i)\n" +
                    "    })\n" +
                    "}\n" +
                    " \n" +
                    "funArr.forEach((item)=>{\n" +
                    "    item()\n" +
                    "})\n" +
                    "```"
            },
            {
                path:"/closure",
                text:"# javascript base\n"+
                    "## \n" +
                    "\n" +
                    "**if{}**\n" +
                    " JavaScript JavaScript closure\n" +
                    "```javascript\n" +
                    "//forvar\n" +
                    "let funArr = [];\n" +
                    "for(var i = 0;i<5;i++){\n" +
                    "    funArr.push(function(){\n" +
                    "        console.log(i)\n" +
                    "    })\n" +
                    "}\n" +
                    " \n" +
                    "funArr.forEach((item)=>{\n" +
                    "    item()\n" +
                    "})\n" +
                    "\n" +
                    "//letfunArrifunArri\n" +
                    "//ii\n" +
                    "let funArr = [];\n" +
                    "for(let i = 0;i<5;i++){\n" +
                    "  funArr.push(function(){\n" +
                    "    console.log(i)\n" +
                    "  })\n" +
                    "}\n" +
                    "\n" +
                    "funArr.forEach(item =>{\n" +
                    "  item()\n" +
                    "})\n" +
                    "//1   2   3   4   5\n" +
                    "```"
            },
            {
                path: "/react_base",
                text: "# React base"
            },
            {
                path: "/component_base",
                text: "# component base"
            }
        ]
    }
    render() {
        const components = {
            // 
            h1: ({ node, ...props }) => <h1 className="text-5xl my-2 font-sans font-bold subpixel-antialiased ">{props.children}</h1>,
            h2: ({ node, ...props }) => <h2 className="text-3xl my-3 font-sans font-bold subpixel-antialiased ">{props.children}</h2>,
            h3: ({ node, ...props }) => <h3 className="text-2xl my-4 font-sans font-bold subpixel-antialiased ">{props.children}</h3>,
            // 
            a:({ node, ...props }) => <a href={node.properties.href} className="text-xl font-sans subpixel-antialiased text-sky-400 hover:text-sky-700 hover:underline">{props.children}</a>,
            p: ({node, ...props}) => <p className="text-xl my-2 font-sans subpixel-antialiased">{props.children}</p>,
            ul:({ node, ...props }) => <ul className="text-xl my-2 font-sans subpixel-antialiased list-outside list-disc px-10 py-6 bg-slate-100 rounded-2xl mx-6 shadow-2xl">{props.children}</ul>,
            ol:({ node, ...props }) => <ol className="text-xl my-2 font-sans subpixel-antialiased list-outside px-10">{props.children}</ol>,
            li:({ node, ...props }) => <li className="text-xl my-2 font-sans subpixel-antialiased text-left first:list-none first:text-2xl first:-ml-6">{props.children}</li>,
            pre:({ node, ...props }) => <pre className="my-2 px-4 mx-6">{props.children}</pre>,
            code:({ node, inline, className, children, ...props })=>{
                const match = /language-(\w+)/.exec(className || '');
                return !inline && match ? (
                    <SyntaxHighlighter
                        style={coy}
                        language={match[1]}
                        PreTag="div"
                        className="font-sans subpixel-antialiased py-6 bg-slate-100 rounded-2xl shadow-xl bg-slate-50"
                        {...props}
                    >
                        {String(children).replace(/\n$/, '')}
                    </SyntaxHighlighter>
                ) : (
                    <code className="ring-1 ring-slate-300 px-2 mx-1 rounded bg-slate-100" {...props}>
                        {children}
                    </code>
                );
            }
            //  Markdown ...
        }
        console.log(this.props)
        const text=this.state.markdowntext.find((obj)=>{
            // console.log(obj)
            return obj.path===this.props.match.path
        }).text
        return (
            <div>
                <Markdown components={components} children={text}></Markdown>
            </div>
        );
    }
}

export default Mark;